import { ReadableSpan } from '@opentelemetry/sdk-trace-base';
import debug from 'debug';
export const SPANS_EXPORT_NS = 'aspecto:spans:export';

export function spanLogDetails(span: ReadableSpan) {
    if (!debug.enabled(SPANS_EXPORT_NS)) return null;

    // the default DEBUG_DEPTH=2 and we don't want to have overwhelming output
    return {
        details: {
            span: {
                name: span.name,
                kind: span.kind,
                spanContext: span.spanContext,
                parentSpanId: span.parentSpanId,
                startTime: span.startTime,
                endTime: span.endTime,
                status: span.status,
                attributes: span.attributes,
                links: span.links,
                events: span.events,
                duration: span.duration,
                ended: span.ended,
                resource: span.resource,
                instrumentationLibrary: span.instrumentationLibrary,
            },
        },
    };
}
